import React, { Component } from "react";
import PageHeader from "../Elements/PageHeader";
import Projectdata from "../Json/Projectdata";

class AppPage extends Component {
    constructor(props){
        super(props);

        let { id } = this.props.match.params;

        Projectdata.forEach(element => {
            if (element.page === id){
                this.title = element.title;
                this.image = element.image;
                this.desc = element.desc;
                this.link = element.link;
            }
        });
    }
    render() {
        
        return (
            <section id="portfolio" className="two">
                <div className="container">
                    <div className="row">
                        <div className="col-4 col-12-mobile">
                            <PageHeader title={this.title} />

                            <img src={this.image} alt="" className="small _thumb" />
                            
                            <br />
                            
                            <a href={this.link} target="_blank" rel="noopener noreferrer" 
                                className="button scrolly">Download</a>
                        </div>

                        <div className="col-8 col-12-mobile">
                            <br />
                            <br />
                            <p className={"line-text"}>{this.desc.split("\n").map(function(item, key){
                                return(
                                    <span key={key} >
                                        {item}
                                        <br />
                                    </span>
                                )
                            })}</p>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default AppPage;