import React, { Component } from "react";
import PageHeader from "../Elements/PageHeader"
import ProjectTile from "../Elements/ProjectTile"
import ProjectListing from "../Elements/ProjectListing"
import ProjectsData from "../Json/Projectdata"

class Stuff extends Component {
    constructor(props){
        super(props)
        this._project_data = ProjectsData;

    }
    render() {
        // const { id } = this.props.match.params;
        const ProjectsDataItems = this._project_data.map((data, i) =>
            <ProjectTile
                key={i}
                title={data.title}
                image={data.image}
                page={data.page}
                desc={data.desc} />
        );
        return (
            <section id="portfolio" className="two">
                <div className="container">

                    <PageHeader title="My Work.." />
                    {/* todo: add alt to all the images */}
                    <ProjectListing 
                        perColumn={2} >
                        {ProjectsDataItems}
                    </ProjectListing>
                    
                </div>
            </section>
        );
    }
}

export default Stuff;