import React, { Component } from "react";
import PageHeader from "../Elements/PageHeader";
import blogData from "../Json/blogData"

class BlogArticle extends Component {
    constructor(props) {
        super(props);
        let { id }= this.props.match.params;
        this.id = id;
        this.state = {
            title: "", 
            link: "", 
            date: "", 
            content:"",
            page:""
        };
        
    }
    componentDidMount(){
        this.htmlShow = this.getblogData()
    }
    createMarkup() {
        return { __html: this.state.content };
    }
    getblogData() {
        
        let blogItems = blogData.rss.channel.item;
        
        for (let i = 0; i < blogItems.length; i++) {
            let item = blogItems[i];
            let page = item["wp:post_name"];
            
            if (page === this.id) {
                
                this.setState({
                    title: item.title,
                    link: item.link,
                    date: new Date(item.pubDate).toDateString(),
                    content: item["content:encoded"].__cdata,
                    page : page
                });
                return 0;
            }
        }
        return blogItems;
    }
    render() {
        
        return (
            <section id="portfolio" className="two">
                <div className="container">
                    <div className="row">
                        <div className="col-12 col-12-mobile">
                            
                            <PageHeader title={this.state.title} />
                            
                            <p>{this.state.date} &nbsp;
                                <a href={this.state.link} target="_blank noopener">
                                    <span className="icon solid fa-link"></span>
                                </a>
                            </p>

                            <div className={"blog_content"} dangerouslySetInnerHTML={this.createMarkup()} />

                            {/* <p>{this.state.page}</p> */}
                            
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default BlogArticle;