import React, { Component } from "react";
import BlogTile from "../Elements/BlogTitle";
import BlogPageListing from "../Elements/BlogPageListing"
import blogData from "../Json/blogData"

class BlogPage extends Component {
    constructor(props){
        super(props)
        
        this.htmlShow = this.getblogData()
    }
    getblogData() {
        let blogItems = blogData.rss.channel.item;
        
        let blogTitles = []
        for (let i = 0; i < blogItems.length; i++) {
            let item = blogItems[i];
            let date = new Date(item.pubDate).toDateString();
            let post_type = item["wp:post_type"];
            
            if (item.pubDate !== "" && post_type === "post") {
                blogTitles.push(
                    <BlogTile
                        key={i}
                        title={item.title}
                        link={item.link}
                        pubDate={date}
                        content={item["content:encoded"]}
                        page={item["wp:post_name"]}
                    />)
            }
        }
        return blogTitles;
    }
    
    render() {
        
        return (
            <section id="top" className="two">
                <div className="container">
                    
                    <BlogPageListing >
                        {this.htmlShow}
                    </BlogPageListing>
                </div>
            </section>
        );
    }
}

export default BlogPage;