import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import HeaderLogo from "./HeaderLogo"
import SocialLink from "./SocialLink"
import SocialLinksIcons from "./SocialLinksIcons"

class Sidebar extends Component {
    constructor(props){
        super(props)
        this._links_data = [
            { name: "Github", class: "icon brands fa-github", link:"https://github.com/naxrohan"},
            { name: "Linkedin", class: "icon brands fa-linkedin", link: "https://www.linkedin.com/in/dsouza-rohan"},
            { name: "Email", class: "icon solid fa-envelope", link: "mailto:rohan.jd@hotmail.com"} 
         ]
    }
    render() {
        const SocialLinks = this._links_data.map((data, i) =>
            <SocialLink
                key={i}
                name={data.name}
                class={data.class}
                link={data.link} />
        );
        return (
            <div id="header">

                <div className="top">

                    <HeaderLogo 
                        image="images/avatar.jpg" 
                        title="Rohan Dsouza" 
                        desig="Full Stack Developer"/>

                    <nav id="nav">
                        <ul>
                            <li><NavLink exact to="/"><span className="icon solid fa-home">Home</span></NavLink></li>
                            <li><NavLink to="/apps"><span className="icon solid fa-th">Apps</span></NavLink></li>
                            {/* <li><NavLink to="/resume"><span className="icon solid fa-file">About</span></NavLink></li> */}
                            <li><NavLink to="/blog"><span className="icon solid fa-blog">Blog</span></NavLink></li>
                        </ul>
                    </nav>

                </div>

                <SocialLinksIcons >
                    {SocialLinks}
                 </SocialLinksIcons>
            </div>
        );
    }
}

export default Sidebar;