import React, { Component } from "react";

class BlogPageListing extends Component {

    render() {
        const childLinks = this.props.children
        return (
            <div className="row">
                {React.Children.map(childLinks, (child, i) => {
                    return <div className="col-12 col-12-mobile">{child}</div>
                })}
            </div>
        );
    }
}

export default BlogPageListing;