import React, { Component } from "react";
import { Route, HashRouter } from "react-router-dom";
import Home from "./Pages/Home";
import Stuff from "./Pages/Stuff";
import Resume from "./Pages/Resume"
import Sidebar from "./Elements/Sidebar";
import PageFooter from "./Elements/PageFooter"
import AppPage from "./Pages/AppPage";
import BlogPage from "./Pages/BlogPage";
import BlogArticle from "./Pages/BlogArticle";
import PrivacyPage from "./Pages/PrivacyPage";
import MobileMenu from "./Elements/MobileMenu";


class Main extends Component {
    render() {
        return (
            <HashRouter>
                
                <div className="mainpage">
                    <Sidebar />
                    
                    <div id="main">
                        
                        <Route exact path="/" component={Home} />
                        <Route exact path="/apps" component={Stuff} />
                        <Route exact path="/app/:id?" component={AppPage} />
                        <Route exact path="/resume" component={Resume} />
                        <Route exact path="/blog" component={BlogPage} />
                        <Route exact path="/blog-page/:id?" component={BlogArticle} />
                        <Route exact path="/privacy" component={PrivacyPage} />
                        <Route exact path="/header" component={MobileMenu} />
                    </div>
                </div>

                
                <PageFooter />

            </HashRouter>
        );
    }
}

export default Main;